import React, { useState, useEffect } from "react";
import { Form, Transition, Dropdown } from "semantic-ui-react";
import { withRouter } from "react-router-dom";
import { authRegister, updateUserProfile } from "../../../store/actions";
import { connect } from "react-redux";
import "./ProfileForm.scss";
import appRoutes from "../../../configs/routes";

const organisationSize = [
  {
    key: "1",
    text: "2 - 10",
    value: "10",
  },
  {
    key: "2",
    text: "11 - 50",
    value: "50",
  },
  {
    key: "3",
    text: "51 - 200",
    value: "200",
  },
  {
    key: "4",
    text: "201 - 1000",
    value: "1000",
  },
  {
    key: "5",
    text: "1000+",
    value: "1000+",
  },
];

const ProfileForm = React.memo((props) => {
  const [nameErr, setNameErr] = useState(null);
  const [organizationErr, setOrganizationErr] = useState(null);
  const [organizationSizeErr, setOrganizationSizeErr] = useState(null);
  const [signupCreds, setSignupCreds] = useState({
    name: "",
    organization: "",
    organizationSize: "",
  });

  const [isError, setisError] = useState(true);

  useEffect(() => {
    validateName();
    validateOrganization();
    validateOrganizationSize();
  }, [signupCreds]);

  const resetErrs = () => {
    setNameErr(null);
    setOrganizationErr(null);
    setisError(false);
  };

  const validateName = (show = false) => {
    setNameErr(null);
    setisError(false);
    if (!signupCreds.name) {
      show &&
        setNameErr({
          content: "Enter your full name",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const validateOrganization = (show = false) => {
    setOrganizationSizeErr(null);
    setisError(false);
    if (!signupCreds.organization) {
      show &&
        setOrganizationSizeErr({
          content: "Enter your organization name",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const validateOrganizationSize = (show = false) => {
    setOrganizationErr(null);
    setisError(false);
    if (!signupCreds.organizationSize) {
      show &&
        setOrganizationErr({
          content: "Enter your organization size",
          pointing: "below",
        });
      setisError(true);
    }
  };

  const validateForm = (e) => {
    e.preventDefault();
    resetErrs();
    validateName(true);
    validateOrganization(true);
    validateOrganizationSize(true);

    if (!isError) {
      props.submitted(signupCreds);
    }
  };

  return (
    <Transition.Group animation="horizontal flip" duration="300">
      <Form className="signup-form" onSubmit={validateForm}>
        <React.Fragment>
          <Form.Field>
            <Form.Input
              fluid
              icon="user outline"
              iconPosition="left"
              placeholder="Full name"
              value={signupCreds.name}
              error={nameErr}
              style={{
                margin: "2rem 0",
              }}
              onChange={(event) => {
                const _name = event.target.value;
                setSignupCreds((prev) => ({
                  ...prev,
                  name: _name,
                }));
                validateName();
              }}
            />
          </Form.Field>
          <Form.Field>
            <Form.Input
              fluid
              icon="building outline"
              iconPosition="left"
              placeholder="Organization Name"
              value={signupCreds.organization}
              error={organizationErr}
              style={{
                margin: "2rem 0",
              }}
              onChange={(event) => {
                const _organization = event.target.value;
                setSignupCreds((prev) => ({
                  ...prev,
                  organization: _organization,
                }));
                validateOrganization();
              }}
            />
          </Form.Field>
          <Form.Field>
            <Dropdown
              icon="group"
              iconPosition="left"
              placeholder="Organization Size"
              fluid
              selection
              options={organisationSize}
              value={signupCreds.organizationSize}
              error={nameErr}
              style={{
                margin: "2rem 0",
              }}
              className="profileForm-organisationSize"
              onChange={(event, data) => {
                console.log(event, data);
                const _organizationSize = data.value;
                setSignupCreds((prev) => ({
                  ...prev,
                  organizationSize: _organizationSize,
                }));
                validateOrganizationSize();
              }}
            />
          </Form.Field>
          <Form.Field>
            <Form.Button
              fluid
              iconPosition="left"
              color="green"
              loading={props.isLoading}
              disabled={isError}
              onClick={validateForm}
            >
              Save Profile
            </Form.Button>
          </Form.Field>
        </React.Fragment>
      </Form>
    </Transition.Group>
  );
});

export default withRouter(ProfileForm);
